body {
  background: #fcfcfc;
  height: 100%;
  margin: 0;
}
.gridCont {
  display: grid;
  grid-template-columns: minmax(270px, 1.5fr) 4fr;
  grid-template-rows: 1fr;
  padding: 119px 0px 40px 0px;
}
.header {
  position: fixed;
  width: 100%;
  background-color: #ffffff;
  margin: 0;
  border: solid #f2f2f2;
  border-width: thin;
  text-align: center;
  grid-row-start: span 3;
}
.header p {
  margin: 5px;
}
.header_title {
  margin: 10px 0px;
}
.header_input {
  color: black;
  background-color: transparent;
  border: solid orange;
  border-width: thin;
  padding: 5px;
  border-radius: 8px;
  margin-bottom: 5px;
}
.body_cardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.cardlist {
  width: 100%;
  display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    grid-template-rows: 1fr;
    grid-gap: 0px;
}
.currentPercentageDiv {
  grid-column: 1/-1;
  text-align: center;
}
.currentPercentage {
  font-family: sans-serif;
  font-size: 30px;
  margin: 15px;
  font-weight: 600;
}
.card {
  background-image: linear-gradient(45deg, #FF512F, #F09819);
  background: -webkit-gradient(linear, left top, right top, from(#FF512F), to(#F09819));
  border-radius: 10px;
  padding: 7px;
  margin: 10px;
  text-align: center;
  color: #ffffff;
}
.cardName {
  font-weight: 600;
  font-size: 1.5rem;
  margin: 5px;
}
.Card_input {
	background-color: transparent;
  width: 50%;
	border: solid orange;
	border-width: thin;
	border-radius: 8px;
  text-align: center;
  color: white;
  font-size: 20px;
}
.Card_input::placeholder {
  margin-top: 0;
  font-size: 20px;
	color: #ffffff;
	padding: 0px;
	opacity: .5;
}
.cardTotal{
  font-weight: 600;
  font-size: 2rem;
  margin: .5em;
}
.navigation {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ffffff;
  justify-content: center;
  padding: 5px;
}
.navigation_button {
  background-color: #00cbff;
  font-weight: 800;
  color: white;
  border-radius: 15px;
  border: none;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
}